import React, { useState, useEffect } from 'react';
import { Tag, Settings, Edit2 } from 'lucide-react';
import { tagPresets, propertyPresets, getRecommendedTags, getRecommendedProperties } from './YamlPresets';

const MetadataPanel = ({
  obsidianTags,
  setObsidianTags,
  obsidianProperties,
  setObsidianProperties,
  processedData
}) => {
  const [newTag, setNewTag] = useState('');
  const [newPropertyName, setNewPropertyName] = useState('');
  const [newPropertyValue, setNewPropertyValue] = useState('');
  // 
  const [editingIndex, setEditingIndex] = useState(-1);
  const [editValue, setEditValue] = useState('');

  // Obsidian
  const handleAddTag = () => {
    if (!newTag.trim()) return;
    
    if (!obsidianTags.includes(newTag.trim())) {
      setObsidianTags([...obsidianTags, newTag.trim()]);
    }
    
    setNewTag('');
  };

  // Obsidian
  const handleRemoveTag = (tag) => {
    setObsidianTags(obsidianTags.filter(t => t !== tag));
  };

  // Obsidian
  const handleAddProperty = () => {
    if (!newPropertyName.trim()) return;
    
    // 
    const existingIndex = obsidianProperties.findIndex(p => p.name === newPropertyName.trim());
    
    if (existingIndex >= 0) {
      // 
      if (newPropertyValue.trim()) {
        const updatedProperties = [...obsidianProperties];
        updatedProperties[existingIndex] = {
          name: newPropertyName.trim(),
          value: newPropertyValue.trim()
        };
        setObsidianProperties(updatedProperties);
      }
    } else {
      // 
      setObsidianProperties([
        ...obsidianProperties, 
        { name: newPropertyName.trim(), value: newPropertyValue.trim() || '' }
      ]);
    }
    
    setNewPropertyName('');
    setNewPropertyValue('');
  };

  // Obsidian
  const handleRemoveProperty = (index) => {
    setObsidianProperties(obsidianProperties.filter((_, i) => i !== index));
  };
  
  // 
  const startEditing = (index, initialValue) => {
    setEditingIndex(index);
    setEditValue(initialValue);
  };
  
  // 
  const saveEdit = () => {
    if (editingIndex >= 0) {
      const updatedProperties = [...obsidianProperties];
      updatedProperties[editingIndex] = {
        ...updatedProperties[editingIndex],
        value: editValue
      };
      setObsidianProperties(updatedProperties);
      setEditingIndex(-1);
    }
  };
  
  // 
  const handleRecommendTags = () => {
    if (!processedData) return;
    
    const title = processedData.meta_info.title || '';
    // 10
    const content = processedData.chat_history
      .slice(0, 10)
      .map(msg => msg.display_text)
      .join(' ');
      
    const recommendedTags = getRecommendedTags(title, content);
    setObsidianTags([...new Set([...obsidianTags, ...recommendedTags])]);
  };
  
  // 
  const handleRecommendProperties = () => {
    if (!processedData) return;
    
    const title = processedData.meta_info.title || '';
    // 10
    const content = processedData.chat_history
      .slice(0, 10)
      .map(msg => msg.display_text)
      .join(' ');
      
    const recommendedProps = getRecommendedProperties(title, content);
    
    // 
    const existingNames = new Set(obsidianProperties.map(p => p.name));
    
    // 
    const newProps = recommendedProps
      .filter(p => !existingNames.has(p.name))
      .map(p => ({ name: p.name, value: '' }));
    
    setObsidianProperties([...obsidianProperties, ...newProps]);
  };

  return (
    <div className="p-3 tab-content-container">
      <div className="bg-gray-100 border border-gray-200 rounded p-3 panel-content-area">
        <h3 className="font-medium text-gray-700 mb-3"> (Obsidian)</h3>
        
        <div className="flex space-x-4">
          <div className="border border-gray-200 bg-white rounded p-2 flex-1">
            <div className="flex justify-between mb-2">
              <h4 className="font-medium text-gray-700 flex items-center">
                <Tag className="h-4 w-4 mr-1" />
                
              </h4>
              <button 
                className="text-xs text-blue-600 hover:text-blue-800"
                onClick={handleRecommendTags}
              >
                
              </button>
            </div>
            <div className="flex mb-2">
              <input 
                type="text" 
                placeholder="..." 
                className="flex-1 px-2 py-1 border border-gray-300 rounded-l text-sm"
                value={newTag}
                onChange={(e) => setNewTag(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleAddTag()}
              />
              <button 
                className="bg-blue-500 text-white px-2 py-1 rounded-r text-sm"
                onClick={handleAddTag}
              >
                
              </button>
            </div>
            <div className="max-h-32 overflow-y-auto border border-gray-200 rounded p-2">
              {obsidianTags.length === 0 ? (
                <div className="text-gray-500 text-sm text-center"></div>
              ) : (
                <div className="flex flex-wrap gap-1">
                  {obsidianTags.map((tag, idx) => (
                    <div key={idx} className="bg-blue-100 text-blue-700 rounded px-2 py-0.5 text-xs flex items-center">
                      #{tag}
                      <button 
                        className="ml-1 text-blue-500 hover:text-blue-700"
                        onClick={() => handleRemoveTag(tag)}
                      >
                        
                      </button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
          
          <div className="border border-gray-200 bg-white rounded p-2 flex-1">
            <div className="flex justify-between mb-2">
              <h4 className="font-medium text-gray-700 flex items-center">
                <Settings className="h-4 w-4 mr-1" />
                
              </h4>
              <button 
                className="text-xs text-blue-600 hover:text-blue-800"
                onClick={handleRecommendProperties}
              >
                
              </button>
            </div>
            <div className="flex mb-2">
              <input 
                type="text" 
                placeholder="" 
                className="w-1/3 px-2 py-1 border border-gray-300 rounded-l text-sm"
                value={newPropertyName}
                onChange={(e) => setNewPropertyName(e.target.value)}
              />
              <input 
                type="text" 
                placeholder="" 
                className="flex-1 px-2 py-1 border-t border-b border-gray-300 text-sm"
                value={newPropertyValue}
                onChange={(e) => setNewPropertyValue(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleAddProperty()}
              />
              <button 
                className="bg-blue-500 text-white px-2 py-1 rounded-r text-sm"
                onClick={handleAddProperty}
              >
                
              </button>
            </div>
            <div className="max-h-32 overflow-y-auto border border-gray-200 rounded p-2">
              {obsidianProperties.length === 0 ? (
                <div className="text-gray-500 text-sm text-center"></div>
              ) : (
                <div className="space-y-1">
                  {obsidianProperties.map((prop, idx) => (
                    <div key={idx} className="bg-gray-100 rounded px-2 py-1 text-xs flex justify-between items-center">
                      <span className="font-medium">{prop.name}:</span>
                      {editingIndex === idx ? (
                        <input
                          type="text"
                          className="mx-1 flex-1 px-1 py-0.5 border border-gray-300 rounded text-xs"
                          value={editValue}
                          onChange={(e) => setEditValue(e.target.value)}
                          onBlur={saveEdit}
                          onKeyPress={(e) => e.key === 'Enter' && saveEdit()}
                          autoFocus
                        />
                      ) : (
                        <span 
                          className="mx-1 flex-1 cursor-pointer hover:bg-gray-200 px-1 rounded flex items-center"
                          onClick={() => startEditing(idx, prop.value)}
                          title=""
                        >
                          {prop.value || <span className="text-gray-400 italic">()</span>}
                          <Edit2 className="ml-1 h-3 w-3 text-gray-400" />
                        </span>
                      )}
                      <button 
                        className="text-gray-500 hover:text-gray-700"
                        onClick={() => handleRemoveProperty(idx)}
                      >
                        
                      </button>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        <div className="mt-4 p-3 bg-blue-50 rounded border border-blue-200 text-sm text-gray-700">
          <p className="font-medium mb-1 text-blue-700"></p>
          <p>MarkdownYAMLObsidian</p>
          <p><code className="bg-gray-100 px-1 rounded">tags</code>  | YAML | YAML</p>
        </div>
      </div>
    </div>
  );
};

export default MetadataPanel;