// UserSettings.js - 
// ~~~()

import React, { useState, useEffect } from 'react';
import { Save, RotateCcw, Moon, Sun, Layout, FileText, Tag, Settings as SettingsIcon } from 'lucide-react';

// 
const defaultSettings = {
  // 
  appearance: {
    theme: 'light', // light, dark
    fontSize: 'medium', // small, medium, large
    messageSpacing: 'compact', // compact, comfortable
    showTimestamps: true,
    colorScheme: 'blue', // blue, purple, green, orange
  },
  
  // 
  export: {
    defaultIncludeThinking: true,
    defaultIncludeTools: true,
    defaultIncludeArtifacts: true,
    defaultIncludeCitations: true,
    defaultExportObsidianMetadata: true,
    autoSuggestTags: true,
    autoGenerateTitle: true,
  },
  
  // 
  behavior: {
    autoSaveInterval: 5, // 
    confirmBeforeDelete: true,
    showDeletedMessages: false,
    showCompletedMessages: true,
    autoExpandThinking: false,
    autoScrollToSelected: true,
    rememberLastFile: true,
  },
  
  // 
  tagPresets: {
    enabledGroups: ['tech', 'claude', 'personal'],
    customTags: [],
  },
  
  // 
  shortcuts: {
    markCompleted: 'ctrl+d',
    markImportant: 'ctrl+i',
    deleteMessage: 'ctrl+delete',
    copySelected: 'ctrl+c',
    exportSelected: 'ctrl+e',
    findInPage: 'ctrl+f',
    globalSearch: 'ctrl+shift+f',
  },
};

// localStorage
export const loadSettings = () => {
  try {
    const savedSettings = localStorage.getItem('claude_chat_exporter_settings');
    if (savedSettings) {
      return { ...defaultSettings, ...JSON.parse(savedSettings) };
    }
  } catch (error) {
    console.error(':', error);
  }
  return defaultSettings;
};

// localStorage
export const saveSettings = (settings) => {
  try {
    localStorage.setItem('claude_chat_exporter_settings', JSON.stringify(settings));
    return true;
  } catch (error) {
    console.error(':', error);
    return false;
  }
};

// 
const UserSettingsPanel = ({ currentSettings, onSave, onCancel }) => {
  const [settings, setSettings] = useState(currentSettings);
  const [activeTab, setActiveTab] = useState('export');
  
  // 
  const handleChange = (section, key, value) => {
    setSettings(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [key]: value
      }
    }));
  };
  
  // 
  const handleReset = () => {
    if (window.confirm('')) {
      setSettings(defaultSettings);
    }
  };
  
  // 
  const handleAddCustomTag = (tag) => {
    if (!tag || settings.tagPresets.customTags.includes(tag)) return;
    
    setSettings(prev => ({
      ...prev,
      tagPresets: {
        ...prev.tagPresets,
        customTags: [...prev.tagPresets.customTags, tag]
      }
    }));
  };
  
  // 
  const handleRemoveCustomTag = (tag) => {
    setSettings(prev => ({
      ...prev,
      tagPresets: {
        ...prev.tagPresets,
        customTags: prev.tagPresets.customTags.filter(t => t !== tag)
      }
    }));
  };
  
  // 
  const handleToggleTagGroup = (groupName) => {
    setSettings(prev => ({
      ...prev,
      tagPresets: {
        ...prev.tagPresets,
        enabledGroups: prev.tagPresets.enabledGroups.includes(groupName)
          ? prev.tagPresets.enabledGroups.filter(g => g !== groupName)
          : [...prev.tagPresets.enabledGroups, groupName]
      }
    }));
  };
  
  return (
    <div className="p-4 bg-gray-50 rounded-lg border border-gray-200 max-h-[80vh] overflow-auto">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-xl font-bold text-gray-800 flex items-center">
          <SettingsIcon className="h-5 w-5 mr-2" />
          
        </h2>
        <div className="flex space-x-2">
          <button 
            className="px-3 py-1 bg-blue-500 text-white rounded hover:bg-blue-600 flex items-center text-sm"
            onClick={() => onSave(settings)}
          >
            <Save className="h-4 w-4 mr-1" />
            
          </button>
          <button 
            className="px-3 py-1 bg-gray-200 text-gray-700 rounded hover:bg-gray-300 flex items-center text-sm"
            onClick={handleReset}
          >
            <RotateCcw className="h-4 w-4 mr-1" />
            
          </button>
          <button 
            className="px-3 py-1 bg-gray-200 text-gray-700 rounded hover:bg-gray-300 text-sm"
            onClick={onCancel}
          >
            
          </button>
        </div>
      </div>
      
  {/*  -  */}
      <div className="flex space-x-1 mb-4 border-b border-gray-200">
        <button 
          className={`px-3 py-2 text-sm font-medium rounded-t ${
            activeTab === 'export' ? 'bg-white border border-gray-200 border-b-white' : 'bg-gray-100 text-gray-600'
          }`}
          onClick={() => setActiveTab('export')}
        >
          <div className="flex items-center">
            <FileText className="h-4 w-4 mr-1" />
            
          </div>
        </button>
        <button 
          className={`px-3 py-2 text-sm font-medium rounded-t ${
            activeTab === 'behavior' ? 'bg-white border border-gray-200 border-b-white' : 'bg-gray-100 text-gray-600'
          }`}
          onClick={() => setActiveTab('behavior')}
        >
          <div className="flex items-center">
            <Layout className="h-4 w-4 mr-1" />
            
          </div>
        </button>
        <button 
          className={`px-3 py-2 text-sm font-medium rounded-t ${
            activeTab === 'tags' ? 'bg-white border border-gray-200 border-b-white' : 'bg-gray-100 text-gray-600'
          }`}
          onClick={() => setActiveTab('tags')}
        >
          <div className="flex items-center">
            <Tag className="h-4 w-4 mr-1" />
            
          </div>
        </button>
      </div>
      
      {/*  */}
      {activeTab === 'appearance' && (
        <div className="bg-white p-4 rounded border border-gray-200">
          <h3 className="font-medium text-gray-800 mb-3"></h3>
          
          <div className="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1"></label>
              <div className="flex space-x-2">
                <button
                  className={`flex-1 py-2 px-3 rounded flex items-center justify-center ${
                    settings.appearance.theme === 'light' 
                      ? 'bg-blue-100 text-blue-700 border border-blue-300' 
                      : 'bg-gray-100 text-gray-700 border border-gray-200'
                  }`}
                  onClick={() => handleChange('appearance', 'theme', 'light')}
                >
                  <Sun className="h-4 w-4 mr-2" />
                  
                </button>
                <button
                  className={`flex-1 py-2 px-3 rounded flex items-center justify-center ${
                    settings.appearance.theme === 'dark' 
                      ? 'bg-blue-100 text-blue-700 border border-blue-300' 
                      : 'bg-gray-100 text-gray-700 border border-gray-200'
                  }`}
                  onClick={() => handleChange('appearance', 'theme', 'dark')}
                >
                  <Moon className="h-4 w-4 mr-2" />
                  
                </button>
              </div>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1"></label>
              <select
                className="w-full border border-gray-300 rounded p-2 text-sm"
                value={settings.appearance.fontSize}
                onChange={(e) => handleChange('appearance', 'fontSize', e.target.value)}
              >
                <option value="small"></option>
                <option value="medium"></option>
                <option value="large"></option>
              </select>
            </div>
          </div>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1"></label>
            <div className="flex space-x-3">
              {['blue', 'purple', 'green', 'orange'].map(color => (
                <button
                  key={color}
                  className={`w-8 h-8 rounded-full border-2 ${
                    settings.appearance.colorScheme === color 
                      ? 'border-gray-800' 
                      : 'border-transparent'
                  }`}
                  style={{ 
                    backgroundColor: 
                      color === 'blue' ? '#3b82f6' : 
                      color === 'purple' ? '#8b5cf6' : 
                      color === 'green' ? '#10b981' : 
                      '#f97316'
                  }}
                  onClick={() => handleChange('appearance', 'colorScheme', color)}
                />
              ))}
            </div>
          </div>
          
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-1"></label>
            <div className="flex space-x-2">
              <button
                className={`flex-1 py-2 px-3 rounded ${
                  settings.appearance.messageSpacing === 'compact' 
                    ? 'bg-blue-100 text-blue-700 border border-blue-300' 
                    : 'bg-gray-100 text-gray-700 border border-gray-200'
                }`}
                onClick={() => handleChange('appearance', 'messageSpacing', 'compact')}
              >
                
              </button>
              <button
                className={`flex-1 py-2 px-3 rounded ${
                  settings.appearance.messageSpacing === 'comfortable' 
                    ? 'bg-blue-100 text-blue-700 border border-blue-300' 
                    : 'bg-gray-100 text-gray-700 border border-gray-200'
                }`}
                onClick={() => handleChange('appearance', 'messageSpacing', 'comfortable')}
              >
                
              </button>
            </div>
          </div>
          
          <div className="flex items-center mb-2">
            <input 
              type="checkbox" 
              id="show-timestamps" 
              checked={settings.appearance.showTimestamps}
              onChange={(e) => handleChange('appearance', 'showTimestamps', e.target.checked)}
              className="mr-2" 
            />
            <label htmlFor="show-timestamps" className="text-sm"></label>
          </div>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'export' && (
        <div className="bg-white p-4 rounded border border-gray-200" style={{ minHeight: '300px' }}>
          <h3 className="font-medium text-gray-800 mb-3"></h3>
          
          <div className="grid grid-cols-2 gap-y-2 mb-4">
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="default-include-thinking" 
                checked={settings.export.defaultIncludeThinking}
                onChange={(e) => handleChange('export', 'defaultIncludeThinking', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="default-include-thinking" className="text-sm"></label>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="default-include-tools" 
                checked={settings.export.defaultIncludeTools}
                onChange={(e) => handleChange('export', 'defaultIncludeTools', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="default-include-tools" className="text-sm"></label>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="default-include-artifacts" 
                checked={settings.export.defaultIncludeArtifacts}
                onChange={(e) => handleChange('export', 'defaultIncludeArtifacts', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="default-include-artifacts" className="text-sm">Artifacts</label>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="default-include-citations" 
                checked={settings.export.defaultIncludeCitations}
                onChange={(e) => handleChange('export', 'defaultIncludeCitations', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="default-include-citations" className="text-sm"></label>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="default-obsidian-metadata" 
                checked={settings.export.defaultExportObsidianMetadata}
                onChange={(e) => handleChange('export', 'defaultExportObsidianMetadata', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="default-obsidian-metadata" className="text-sm">Obsidian</label>
            </div>
          </div>
          
          <h3 className="font-medium text-gray-800 mb-2"></h3>
          <div className="mb-4">
            <div className="flex items-center mb-2">
              <input 
                type="checkbox" 
                id="auto-suggest-tags" 
                checked={settings.export.autoSuggestTags}
                onChange={(e) => handleChange('export', 'autoSuggestTags', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="auto-suggest-tags" className="text-sm"></label>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="auto-generate-title" 
                checked={settings.export.autoGenerateTitle}
                onChange={(e) => handleChange('export', 'autoGenerateTitle', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="auto-generate-title" className="text-sm"></label>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
          </div>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'behavior' && (
        <div className="bg-white p-4 rounded border border-gray-200" style={{ minHeight: '300px' }}>
          <h3 className="font-medium text-gray-800 mb-3"></h3>
          
          <div className="mb-3">
            <label className="block text-sm font-medium text-gray-700 mb-1"> ()</label>
            <div className="flex items-center">
              <input 
                type="number" 
                min="1" 
                max="60"
                className="w-full border border-gray-300 rounded p-2 text-sm"
                value={settings.behavior.autoSaveInterval}
                onChange={(e) => handleChange('behavior', 'autoSaveInterval', parseInt(e.target.value) || 5)}
                disabled={true}
              />
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
          </div>
          
          <div className="grid grid-cols-1 gap-y-2 mb-4">
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="confirm-before-delete" 
                checked={settings.behavior.confirmBeforeDelete}
                onChange={(e) => handleChange('behavior', 'confirmBeforeDelete', e.target.checked)}
                className="mr-2"
                disabled={true}
              />
              <label htmlFor="confirm-before-delete" className="text-sm"></label>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="show-deleted-messages" 
                checked={settings.behavior.showDeletedMessages}
                onChange={(e) => handleChange('behavior', 'showDeletedMessages', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="show-deleted-messages" className="text-sm"></label>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="show-completed-messages" 
                checked={settings.behavior.showCompletedMessages}
                onChange={(e) => handleChange('behavior', 'showCompletedMessages', e.target.checked)}
                className="mr-2" 
              />
              <label htmlFor="show-completed-messages" className="text-sm"></label>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="auto-expand-thinking" 
                checked={settings.behavior.autoExpandThinking}
                onChange={(e) => handleChange('behavior', 'autoExpandThinking', e.target.checked)}
                className="mr-2"
                disabled={true}
              />
              <label htmlFor="auto-expand-thinking" className="text-sm"></label>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="auto-scroll-to-selected" 
                checked={settings.behavior.autoScrollToSelected}
                onChange={(e) => handleChange('behavior', 'autoScrollToSelected', e.target.checked)}
                className="mr-2"
                disabled={true}
              />
              <label htmlFor="auto-scroll-to-selected" className="text-sm"></label>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
            
            <div className="flex items-center">
              <input 
                type="checkbox" 
                id="remember-last-file" 
                checked={settings.behavior.rememberLastFile}
                onChange={(e) => handleChange('behavior', 'rememberLastFile', e.target.checked)}
                className="mr-2"
                disabled={true}
              />
              <label htmlFor="remember-last-file" className="text-sm"></label>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
            </div>
          </div>
        </div>
      )}
      
      {/*  */}
      {activeTab === 'tags' && (
        <div className="bg-white p-4 rounded border border-gray-200" style={{ minHeight: '300px' }}>
          <h3 className="font-medium text-gray-800 mb-3"></h3>
          
          <div className="grid grid-cols-2 gap-2 mb-4">
            {Object.keys(defaultSettings.tagPresets.enabledGroups).map(group => (
              <div key={group} className="flex items-center">
                <input 
                  type="checkbox" 
                  id={`tag-group-${group}`} 
                  checked={settings.tagPresets.enabledGroups.includes(group)}
                  onChange={() => handleToggleTagGroup(group)}
                  className="mr-2"
                  disabled={true}
                />
                <label htmlFor={`tag-group-${group}`} className="text-sm capitalize">
                  {group === 'tech' ? '' : 
                   group === 'writing' ? '' :
                   group === 'research' ? '' :
                   group === 'project' ? '' :
                   group === 'learning' ? '' :
                   group === 'claude' ? 'Claude' :
                   group === 'thinking' ? '' :
                   group === 'personal' ? '' : group}
                </label>
                <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full"></span>
              </div>
            ))}
          </div>
          
          <h3 className="font-medium text-gray-800 mb-2"></h3>
          <div className="mb-4">
            <div className="flex mb-2">
              <input 
                type="text" 
                id="new-custom-tag"
                placeholder="..."
                className="flex-1 border border-gray-300 rounded-l p-2 text-sm"
                onKeyPress={(e) => e.key === 'Enter' && handleAddCustomTag(e.target.value)}
                disabled={true}
              />
              <button
                className="bg-blue-500 text-white px-3 py-2 rounded-r text-sm opacity-50 cursor-not-allowed"
                disabled={true}
              >
                
              </button>
              <span className="ml-2 bg-yellow-100 text-yellow-800 text-xs px-2 py-0.5 rounded-full flex items-center"></span>
            </div>
            
            <div className="flex flex-wrap gap-1 p-2 border border-gray-200 rounded min-h-[100px] max-h-[150px] overflow-y-auto">
              <div className="text-gray-500 text-sm w-full text-center py-3">
                
              </div>
            </div>
          </div>
          
          <div className="mt-4 bg-blue-50 p-3 rounded border border-blue-100 text-sm">
            <div className="font-medium text-blue-700 mb-1"></div>
            <p></p>
          </div>
        </div>
      )}
    </div>
  );
};

export default UserSettingsPanel;