//  - 
export const findRelevantTagGroups = (title, content) => {
  const text = (title + " " + content).toLowerCase();
  const relevantGroups = [];
  
  // 
  if (/python|javascript|code|program|develop|api|data|algorithm/i.test(text)) {
    relevantGroups.push('tech');
  }
  
  if (/write|story|novel|plot|character|creative|blog|article|poetry/i.test(text)) {
    relevantGroups.push('writing');
  }
  
  if (/research|study|paper|science|theory|hypothesis|experiment|analysis/i.test(text)) {
    relevantGroups.push('research');
  }
  
  if (/project|plan|manage|team|timeline|milestone|collaboration|task/i.test(text)) {
    relevantGroups.push('project');
  }
  
  if (/learn|study|course|tutorial|education|knowledge|concept|understand/i.test(text)) {
    relevantGroups.push('learning');
  }
  
  if (/claude|ai|assistant|anthropic|prompt|model|language|llm|thinking/i.test(text)) {
    relevantGroups.push('claude');
  }
  
  if (/idea|thought|concept|philosophy|insight|reflect|analysis|strategy/i.test(text)) {
    relevantGroups.push('thinking');
  }
  
  if (/personal|inspiration|motivation|goal|habit|experience|journey/i.test(text)) {
    relevantGroups.push('personal');
  }
  
  // claudepersonal
  if (relevantGroups.length === 0) {
    relevantGroups.push('claude');
    relevantGroups.push('personal');
  }
  
  return relevantGroups;
};

// 
export const getRecommendedTags = (title, content, maxTags = 8) => {
  // 
  const combinedText = (title + ' ' + content).toLowerCase();
  const keywords = extractKeywords(combinedText);
  
  // 
  const matchedTags = new Set();
  const relevantGroups = findRelevantTagGroups(title, content);
  
  // 
  const allGroupTags = [];
  relevantGroups.forEach(group => {
    allGroupTags.push(...(tagPresets[group] || []));
  });
  
  // 
  keywords.forEach(keyword => {
    allGroupTags.forEach(tag => {
      // 
      if (tag.includes(keyword) || keyword.includes(tag)) {
        matchedTags.add(tag);
      }
    });
  });
  
  // 
  if (matchedTags.size < 3) {
    relevantGroups.forEach(group => {
      const groupTags = tagPresets[group] || [];
      const tagsToAdd = groupTags.slice(0, 2); // 2
      tagsToAdd.forEach(tag => matchedTags.add(tag));
    });
  }
  
  // 
  return [...matchedTags].slice(0, maxTags);
};

// 
const extractKeywords = (text) => {
  // 
  // 1. 
  const stopWords = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', 
                    'the', 'is', 'are', 'and', 'for', 'with', 'that', 'this', 'to', 'of', 'in', 'on', 'at'];
  
  // 2. 
  const words = text.split(/\s+|[,.;:'"!?()""]/g)
    .filter(word => word.length > 1) // 
    .filter(word => !stopWords.includes(word)); // 
  
  // 3. 
  const wordFreq = {};
  words.forEach(word => {
    wordFreq[word] = (wordFreq[word] || 0) + 1;
  });
  
  // 4. 20
  return Object.entries(wordFreq)
    .sort((a, b) => b[1] - a[1])
    .slice(0, 20)
    .map(entry => entry[0]);
};

// 
export const getRecommendedProperties = (title, content) => {
  const relevantGroups = findRelevantTagGroups(title, content);
  const allProperties = [...propertyPresets.basic]; // 
  
  // 
  if (relevantGroups.includes('claude')) {
    allProperties.push(...propertyPresets.claude);
  }
  
  if (relevantGroups.includes('research')) {
    allProperties.push(...propertyPresets.research);
  }
  
  if (relevantGroups.includes('project')) {
    allProperties.push(...propertyPresets.project);
  }
  
  // name
  const uniqueProperties = [];
  const propertyNames = new Set();
  
  allProperties.forEach(prop => {
    if (!propertyNames.has(prop.name)) {
      propertyNames.add(prop.name);
      uniqueProperties.push(prop);
    }
  });
  
  return uniqueProperties;
};